import Vue from "vue";
export default {
  namespaced: true,
  state: {
    itemOfertas: [],
    itemOferta: {},
  },
  getters: {
    itemOfertas: (state) => state.itemOfertas,
    itemOferta: (state) => state.itemOferta,
  },
  mutations: {
    updateItemOfertas(state, itemOfertas) {
      state.itemOfertas = itemOfertas;
    },
    updateItemOferta(state, itemOferta) {
      state.itemOferta = itemOferta;
    },
  },
  actions: {
    loadItemOfertas: async function({ commit }, id) {
      const result = await Vue.axios.get(`/item_ofertas/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateItemOfertas", result.data.itemOfertas);
      }
      return result.data;
    },

    insertItemOferta: async function({ commit }, itemOferta) {
      const result = await Vue.axios.post("/item_ofertas", itemOferta);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateItemOferta", {});
      }
      return result.data;
    },
    modifyItemOferta: async function({ commit }, itemOferta) {
      const result = await Vue.axios.put(
        `/item_ofertas/${itemOferta.id}`,
        itemOferta
      );
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateItemOferta", {});
      }
      return result.data;
    },
    deleteItemOferta: async function({ commit }, id) {
      const result = await Vue.axios.delete(`/item_ofertas/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateItemOferta", {});
      }
      return result.data;
    },
  },
};
