import Vue from "vue";
export default {
  namespaced: true,
  state: {
    ofertasPeliculas: [],
    ofertasEspecial: [],
    ofertasSeries: [],
    ofertasStreaming: [],
    ofertas: [],
    oferta: {},
    cant: null,
  },
  getters: {
    ofertas: (state) => state.ofertas,
    ofertasPeliculas: (state) => state.ofertasPeliculas,
    ofertasEspecial: (state) => state.ofertasEspecial,
    ofertasSeries: (state) => state.ofertasSeries,
    ofertasStreaming: (state) => state.ofertasStreaming,
    oferta: (state) => state.oferta,
    cant: (state) => state.cant,
  },
  mutations: {
    loadOfertas(state, ofertas) {
      state.ofertas = ofertas;
    },
    updateOferta(state, oferta) {
      state.oferta = oferta;
    },
    updateCant(state, cant) {
      state.cant = cant;
    },
    updateOfertasPelicula(state, ofertas) {
      state.ofertasPeliculas = ofertas;
    },
    updateOfertaEspecial(state, ofertas) {
      state.ofertasEspecial = ofertas;
    },
    updateOfertaSeries(state, ofertas) {
      state.ofertasSeries = ofertas;
    },
    updateOfertaStreaming(state, ofertas) {
      state.ofertasStreaming = ofertas;
    },
  },
  actions: {
    loadOfertas: async function({ commit }) {
      const result = await Vue.axios.get("/ofertas");
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("loadOfertas", result.data.ofertas);
      }
    },
    loadOfertasTipo: async function({ commit }, tipo) {
      const result = await Vue.axios.get(`/ofertas-tipo/${tipo}`);
      if (result.data.error) {
        alert("error al cargar las ofertas por tipo: " + result.error_object);
      } else {
        switch (tipo) {
          case 0:
            commit("updateOfertasPelicula", result.data.ofertas);
            break;
          case 1:
            commit("updateOfertaEspecial", result.data.ofertas);
            break;
          case 2:
            commit("updateOfertaSeries", result.data.ofertas);
            break;
          case 3:
            commit("updateOfertaStreaming", result.data.ofertas);
            break;
          default:
            break;
        }
      }
    },
    loadOferta: async function({ commit }, id) {
      const result = await Vue.axios.get(`/ofertas/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateOferta", result.data.oferta);
      }
      return result.data;
    },
    insertOferta: async function({ commit }, oferta) {
      const result = await Vue.axios.post("/ofertas", oferta);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateOferta", {});
      }
      return result.data;
    },
    modifyOferta: async function({ commit }, oferta) {
      const result = await Vue.axios.put(`/ofertas/${oferta.id}`, oferta);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateOferta", {});
      }
      return result.data;
    },
    deleteOferta: async function({ commit }, id) {
      const result = await Vue.axios.delete(`/ofertas/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateOferta", {});
      }
      return result.data;
    },
  },
};
