import Vue from "vue";
export default {
  namespaced: true,
  state: {
    emisionesEstrenos: [],
    emisionesSeries: [],
    emisionesContemporaneas: [],
    emisiones: [],
    emision: {},
    cant: null,
  },
  getters: {
    emisiones: (state) => state.emisiones,
    emisionesEstrenos: (state) => state.emisionesEstrenos,
    emisionesSeries: (state) => state.emisionesSeries,
    emisionesContemporaneas: (state) => state.emisionesContemporaneas,
    emision: (state) => state.emision,
    cant: (state) => state.cant,
  },
  mutations: {
    loadEmisiones(state, emisiones) {
      state.emisiones = emisiones;
    },
    updateEmision(state, emision) {
      state.emision = emision;
    },
    updateCant(state, cant) {
      state.cant = cant;
    },
    updateEmisionesEstrenos(state, emisiones) {
      state.emisionesEstrenos = emisiones;
    },
    updateEmisionSeries(state, emisiones) {
      state.emisionesSeries = emisiones;
    },
    updateEmisionContemporaneas(state, emisiones) {
      state.emisionesContemporaneas = emisiones;
    },
  },
  actions: {
    loadEmisiones: async function({ commit }) {
      const result = await Vue.axios.get("/emisiones");
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("loadEmisiones", result.data.emisiones);
      }
    },
    loadEmisionesTipo: async function({ commit }, tipo) {
      const result = await Vue.axios.get(`/emisiones-tipo/${tipo}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        switch (tipo) {
          case 0:
            commit("updateEmisionesEstrenos", result.data.emisiones);
            break;
          case 1:
            commit("updateEmisionSeries", result.data.emisiones);
            break;
          case 2:
            commit("updateEmisionContemporaneas", result.data.emisiones);
            break;
          default:
            break;
        }
      }
    },
    loadEmision: async function({ commit }, id) {
      const result = await Vue.axios.get(`/emisiones/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateEmision", result.data.emision);
      }
      return result.data;
    },
    loadEmisionExistente: async function({ commit }, id) {
      const result = await Vue.axios.get(`/emisiones-existente/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateCant", result.data.cant);
      }
      return result.data;
    },
    insertEmision: async function({ commit }, emision) {
      const result = await Vue.axios.post("/emisiones", emision);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateEmision", {});
      }
      return result.data;
    },
    modifyEmision: async function({ commit }, emision) {
      const result = await Vue.axios.put(`/emisiones/${emision.id}`, emision);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateEmision", {});
      }
      return result.data;
    },
    deleteEmision: async function({ commit }, id) {
      const result = await Vue.axios.delete(`/emisiones/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateEmision", {});
      }
      return result.data;
    },
  },
};
