import Vue from "vue";

export default {
  namespaced: true,
  state: {
    salas: [],
    funciones: [],
    sala: null,
    selSala: null,
    funcion: null,
  },
  getters: {
    salas: (state) => state.salas,
    sala: (state) => state.sala,
    selSala: (state) => state.selSala,
    funciones: (state) => state.funciones,
    funcion: (state) => state.funcion,
  },
  mutations: {
    loadSalas(state, salas) {
      state.salas = salas;
    },
    updateSala(state, sala) {
      state.sala = sala;
    },
    updateSelSala(state, selSala) {
      state.selSala = selSala;
    },
    loadFunciones(state, funciones) {
      state.funciones = funciones;
    },
    updateFuncion(state, funcion) {
      state.funcion = funcion;
    },
  },
  actions: {
    loadSalas: async function({ commit }) {
      const result = await Vue.axios.get("/salas");
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("loadSalas", result.data.salas);
      }
    },
    loadSala: async function({ commit }, id) {
      const result = await Vue.axios.get(`/salas/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateSala", result.data.sala);
      }
      return result.data;
    },
    insertSala: async function({ commit }, sala) {
      const result = await Vue.axios.post("/salas", sala);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateSala", result.data.sala);
      }
      return result.data;
    },
    modifySala: async function({ commit }, sala) {
      const result = await Vue.axios.put(`/salas/${sala.id}`, sala);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateSala", result.data.sala);
      }
      return result.data;
    },
    deleteSala: async function({ commit }, id) {
      const result = await Vue.axios.delete(`/salas/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateSala", result.data.sala);
      }
      return result.data;
    },
    loadFunciones: async function({ commit }) {
      const result = await Vue.axios.get("/funciones");
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("loadFunciones", result.data.funciones);
      }
    },
    loadFuncion: async function({ commit }, id) {
      const result = await Vue.axios.get(`/funciones/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateFuncion", result.data.funcion);
      }
      return result.data;
    },
    loadFuncionAnterior: async function({ commit }, id) {
      const result = await Vue.axios.get(`/funcion-anterior/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateFuncion", result.data.funcion);
      }
      return result.data;
    },
    loadFuncionActual: async function({ commit }, id) {
      const result = await Vue.axios.get(`/funcion-actual/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateFuncion", result.data.funcion);
      }
      return result.data;
    },
    loadFuncionSiguiente: async function({ commit }, id) {
      const result = await Vue.axios.get(`/funcion-siguiente/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateFuncion", result.data.funcion);
      }
      return result.data;
    },
    insertFuncion: async function({ commit }, funcion) {
      const result = await Vue.axios.post("/funciones", funcion);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateFuncion", result.data.funcion);
      }
      return result.data;
    },
    modifyFuncion: async function({ commit }, funcion) {
      const result = await Vue.axios.put(`/funciones/${funcion.id}`, funcion);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateFuncion", result.data.funcion);
      }
      return result.data;
    },
    deleteFuncion: async function({ commit }, id) {
      const result = await Vue.axios.delete(`/funciones/${id}`);
      if (result.data.error) {
        alert(result.error_object);
      } else {
        commit("updateFuncion", result.data.funcion);
      }
      return result.data;
    },
  },
};
